/* Main Content Section*/

.body-container {
    display: grid;
    grid-template-columns: 45% 1fr;
    grid-template-rows: 55vh 39.3vh 1fr;
    background-color: #1A1B21;
}

.main-img {
    width: 44.6vw;
    height: 100%;
}

.info {
    margin: auto;
    text-align: center;
}

.info h1 {
    color: #FFFFFF;
    padding: 3rem 0 1rem;
}

.info h6 {
    color: #F3BF99;
    padding: 1.5rem 0 2rem;
}

.info small {
    color: aliceblue;
    opacity: 80%;
}

a {
    text-decoration: none;
    display: block;
}

.web-link {
    color: #f5f5f5;
    width: fit-content;
    margin: 1rem auto;
}

.info small:hover {
    color: #F3BF99;
}

.btn {
    margin: 2rem 0.938rem;
    width: 7.813rem;
}

.btn span {
    padding: 0 0.625rem;
}

.btn-envelope {
    background-color: #D1D5DB;
}

.btn-envelope a {
    color: #1A1B21;
}

.btn-linkedin {
    background-color: #5093E2;
}

.btn-linkedin a {
    color: #FFFFFF;
}

/* About Section */

.about {
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    text-align: center;
    grid-column-start: 1;
    grid-column-end: 3;
}

.about h4 {
    margin: 5vh auto;
    color: #1A1B21;
}

/* Footer Section */

.footer {
    text-align: center;
    margin: auto auto 0;
    width: 100%;
    grid-column-start: 1;
    grid-column-end: 3;
    background-color: #161619;
}

.footer a {
    display: inline-block;
    padding: 1.25rem;
    color: #918E9B;
}

h4,h6,small{
    font-size: 1.05rem;
}

/* For small devices */

@media screen and (min-device-width: 320px) and (max-device-width: 768px) {
    body{
        background-color: #1A1B21;
    }
    .body-container {
        grid-template-columns: 1fr;
        grid-template-rows: 45% 35% 40% auto;
    }

    .main-img {
        width: 100%;
    }

    .info {
        grid-row-start: 2;
        grid-row-end: 3;
        margin: 12.5rem auto;
    }

    .about {
        background-color: #1A1B21;
        text-align: center;
        margin: 0 auto;
    }

    .about h4 {
        margin: 3.125rem 0;
        padding: 1.875rem 0;
        color: #f5f5f5;
        text-align: center;
    }

    .about small {
        color: aliceblue;
        margin-bottom: 2vw;
        opacity: 80%;
    }

    h4,h6,small{
        font-size: 1.5rem;
    }
}